body {
	-webkit-text-size-adjust: none;
	margin: 0;
	font: 14px/1.5 'Noto Sans KR', 'SCoreDream', sans-serif;
}
h1,h2,h3,h4,h5,h6,p,figure,ul,ol,dl,dd,fieldset,pre {margin: 0;}
ol, ul, td, fieldset {padding: 0;}
input, select, textarea, button, pre, a {
	font : {
		family: inherit;
		size: inherit;
		weight: inherit;
	}
	line-height: inherit;
	letter-spacing: inherit;
	color: inherit;
	text-decoration: none;
}
input, select, textarea {
	margin: 0; padding: 0;
	-webkit-appearance: none;
  border: 1px solid;
	border-radius: 0;
	box-sizing: border-box;
	vertical-align: top;
}
input[type="checkbox"],
input[type="radio"] {
  opacity: 0;
}
select::-ms-expand {display:none;}
li {list-style: none;}
table {
	width: 100%;
	border-collapse: collapse;
	border: 0;
}
fieldset, img {border: 0;}
img {
	vertical-align: top;
	max-width: 100%;
}
label, td img {vertical-align: middle;}
h1,h2,h3,h4,h5,strong,b,th {font-weight: 600;}
legend, caption, .blind {display: none;}
address, em {font-style: normal;}
button {
	margin: 0;
	padding: 0;
	overflow: visible;
	cursor: pointer;
	background: none;
	border: 0;
	border-radius: 0;
	> span {position: relative;}
}
pre {white-space: pre-line;}

.admin {
  &_wrap {
    min-width: 1100px;
  }
  &_header_top {
    position: fixed;
    top: 0; right: 0; left: 130px;
    z-index: 10;
    padding: 0 30px;
    height: 103px;
    background: #fff;
    border-bottom: 1px solid #24262c;
  }
  &_title {
    padding-top: 45px;
    color: #4a4a4a;
    font-size: 24px;
  }
  &_snb {
    position: fixed;
    top: 0; bottom: 0; left: 0;
    z-index: 20;
    width: 130px;
    background: #24262c;
  }
  &_ci {
    position: relative;
    padding-top: 45px;
    height: 103px;
    box-sizing: border-box;
    text-align: center;
    &:after {
      content: "";
      position: absolute;
      right: 0; bottom: 0; left :0;
      border-bottom: 1px solid #fff;
      opacity: .11;
    }
  }
  &_nav {
    margin: 22px 0;
    &_item a {
      display: block;
      padding: 14px 29px 14px 0;
      text-align: right;
      color: #fff;
      font-size: 16px;
    }
    &_item_active {
      background: #005fae;
    }
  }
  &_footer {
    padding-bottom: 58px;
    text-align: center;
    font-size: 11px;
    color: rgba(72, 72, 72, 0.3);
  }
  &_container {
    margin-left: 130px;
    padding: 103px 30px 80px;
  }
  &_paging {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 55px 0 0 -130px;
    .btn_nav {
      position: relative;
      width: 14px; height: 30px;
      font-size: 0;
      margin: 0 4px;
      overflow: hidden;
      &:before, &:after {
        content: "";
        position: absolute;
        top: 50%; left: 50%;
        margin: -5px 0 0 -5px;
        width: 10px; height: 10px;
        border: solid #005fae;
        border-width: 2px 2px 0 0;
        box-sizing: border-box;
      }
      &_start, &_prev {
        &:before, &:after {
          transform: rotate(-135deg);
        }
      }
      &_start:before {margin-left: 0;}
      &_next, &_end {
        &:before, &:after {
          transform: rotate(45deg);
        }
      }
      &_end:before {margin-left: -10px;}
      &_prev:before, &_next:before {display: none;}
      &:disabled {
        opacity: .5;
        cursor: default;
      }
    }
  }
  &_paging_number {
    display: flex;
    margin: 0 5px;
    button {
      min-width: 30px;
      height: 30px;
      box-sizing: border-box;
      border: 1px solid #ececec;
      border-radius: 5px;
      &:disabled {
        color: #005fae;
        cursor: default;
      }
    }
    button ~ button {margin-left: 5px;}
  }
  &_page_title {
    margin: 38px 0 32px;
    font-size: 24px;
    line-height: 1;
    color: #005fae;
  }
  &_search {
    display: flex;
    padding: 18px 32px;
    align-items: center;
    background: #f9f9f9;
    .search_column ~ .search_column {margin-left: -1px;}
    select {
      width: 185px; height: 34px;
      padding: 0 22px;
      background: #fff url('../images/icon/icon_select.png') no-repeat right 12px top 50%;
      border: 1px solid #cdd8db;
      color: #4a4a4a;
    }
    input[type="text"] {
      width: 330px; height: 34px;
      padding: 0 22px;
      border: 1px solid #cdd8db;
    }
    .btn_admin {
      width: 70px; height: 34px;
    }
  }
  &_table_header {
    width: 100%;
    margin: 32px 0 9px;
    .pos_right {
      float: right;
    }
    .btn_admin {padding: 0 32px;}
    &:after {
      content: "";
      display: block;
      clear: both;
    }
  }
  &_table_list {
    overflow-y: hidden;
    overflow-x: auto;
    &::-webkit-scrollbar {
      width: 5px; height: 10px;
    }
    &::-webkit-scrollbar-thumb {
      background: #e9e9e9;
      background-clip: padding-box;
      border: 2px solid transparent;
      border-radius: 5px;
    }
    &::-webkit-scrollbar-thumb:hover {
      background: #4a4a4a;
      border: 2px solid transparent;
      background-clip: padding-box;
    }
    &::-webkit-scrollbar-track {
      background: #f9f9f9;
    }
    table {
      min-width: 1100px;
      text-align: center;
      .left {text-align: left;}
      .right {text-align: right;}
    }
    thead th {
      position: relative;
      padding: 0 5px;
      height: 56px;
      background: #2385d7;
      white-space: nowrap;
      font-size: 13px;
      line-height: 1.23;
      font-weight: 400;
      color: #fff;
      border-bottom: 1px solid #24262c;
      &:before {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        height: 16px;
        border-left: 1px solid #fff;
        opacity: .28;
        transform: translateY(-50%);
      }
      &:first-child:before {display: none;}
    }
    tbody td {
      height: 46px;
      padding: 0 5px;
      background: #f9fafc;
      font-size: 13px;
      color: #4a4a4a;
      white-space: nowrap;
      border-bottom: 1px solid #e9e9e9;
      &.elips {
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .btn {
        display: flex;
        justify-content: space-between;
        width: 100%;
      }
    }
    tbody tr:nth-of-type(2n) td {
      background: #fff;
      border-bottom: 1px solid #f0f0f0;
    }
    .btn_del {
      width: 32px; height: 19px;
      font-size: 12px;
      background: #8f929a;
      color: #fff;
    }
    .btn_file {margin-right: 3px;}
    .btn_file:before {
      content: "";
      display: inline-block;
      width: 20px; height: 19px;
      background: url('../images/icon/icon_file.png') no-repeat;
      margin-right: 4px;
      vertical-align: -4px;
    }
    .btn_cal:after {
      content: "";
      display: inline-block;
      width: 22px; height: 22px;
      background: url('../images/icon/icon_cal2.png') no-repeat;
      margin-left: 4px;
      vertical-align: -4px;
    }
  }
}

.btn_admin {
