  }
}

.btn_wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.input_chk {
  position: relative;
  padding-left: 27px;
  input[type="checkbox"] {
    ~ span:before {
      content: "";
      position: absolute;
      top: 0; left: 0;
      width: 19px; height: 19px;
      background: #fff url('../images/icon/icon_chk2.png') no-repeat 50% 50%;
      border: 1px solid #c1c1c1;
      box-sizing: border-box;
    }
    &:checked ~ span:before {
      background-color: #5f6ee2;
      border-color: #5f6ee2;
    }
  }
  input[type="radio"] {
    ~ span:after,
    ~ span:before {
      content: "";
      position: absolute;
      border-radius: 50%;
    }
    ~ span:before {
      top: 0; left: 0;
      width: 21px; height: 21px;
      background: #fff;
      border: 1px solid #c1c1c1;
      box-sizing: border-box;
    }
    ~ span:after {
      top: 4.5px; left: 4.5px;
      width: 12px; height: 12px;
      background: #e6e6e6;
    }
    &:checked ~ span {
      color: #2f37d3;
    }
    &:checked ~ span:before {
      border: 2px solid #5663e0;
    }
    &:checked ~ span:after {
      background: #5663e0;
    }

  }
}


